"use strict";
exports.PanelView = {
    template: require('./panel.html'),
    props: {
        selected: null,
        options: null
    },
    watch: {
        selected: "onSelGameID"
    },
    gameDataArr: null,
    created: function () {
        var _this = this;
        var apiGame = 'http://api.liangle.com/api/passerbyking/game/list';
        $.get("http://" + window.location.host + "/get?url=" + apiGame, function (res1) {
            var data = JSON.parse(res1.entity);
            console.log(data);
            var gameDataArr = data.data;
            _this.gameDataArr = gameDataArr;
            for (var i = 0; i < gameDataArr.length; i++) {
                var gameData = gameDataArr[i];
                gameData.text = "[" + gameData.id + "]:" + gameData.title;
                gameData.value = gameData.id;
            }
            _this.options = gameDataArr;
        });
    },
    mounted: function () {
    },
    methods: {
        onClkSync: function () {
            console.log('onClkSync');
        },
        onSelGameID: function () {
            $.get("http://" + window.location.host + "/admin/sync/" + this.selected, function (res) {
                console.log(res);
            });
        }
    }
};



