"use strict";
var Navbar_1 = require("./navbar/Navbar");
var home_1 = require("./home/home");
var player_1 = require("./player/player");
var rank_1 = require("./rank/rank");
var panel_1 = require("./panel/panel");
require('../../../resources/app/static/css/bulma.min.css');
var routes = [
    {
        path: '/', name: 'home',
        components: { content: home_1.HomeView, Navbar: Navbar_1.Navbar }
    },
    {
        path: '/player', name: 'player',
        components: { content: player_1.PlayerView, Navbar: Navbar_1.Navbar },
    },
    {
        path: '/panel', name: 'panel',
        components: { content: panel_1.PanelView, Navbar: Navbar_1.Navbar },
    },
    {
        path: '/rank', name: 'rank',
        components: { content: rank_1.RankView, Navbar: Navbar_1.Navbar },
    }
];
var router = new VueRouter({
    routes: routes
});
router.afterEach(function (to, from) {
    var toPath = to.path;
    router.app.active = toPath.split("/")[1];
});
var app = new Vue({
    router: router
}).$mount('#app');



