"use strict";
var const_1 = require("../../const");
var _this_ = exports.Profile;
var $ = require('jquery');
exports.Profile = {
    template: require('./profile.html'),
    props: {
        name: {
            type: String,
            required: true,
        },
        intro: {
            type: String,
            required: true,
        },
        weight: {
            type: String,
            required: true,
        },
        ftId: {},
        ftScore: {},
        curFtScore: {},
        location: {},
        ftOptionArr: {
            type: Array,
            required: true,
            default: []
        },
        height: {
            type: String,
            required: true,
        }
    },
    watch: {
        name: 'onName'
    },
    methods: {
        setProp: function (data, toObj) {
            toObj.name = data.name;
            toObj.weight = data.weight;
            toObj.height = data.height;
            toObj.intro = data.intro;
            toObj.ftId = data.ftId;
            toObj.location = data.location;
            toObj.curFtScore = data.curFtScore;
            toObj.ftScore = data.ftScore;
        },
        onName: function (val) {
            console.log('on name change', val);
        },
        onDeletePlayer: function () {
            console.log('onDeletePlayer');
            this.post('/admin/player/delete', { id: this.editPlayerId }, function (res) {
                if (res == "OK") {
                    console.log('delete sus');
                    window.location.reload();
                }
                else
                    console.error('delete failed');
            });
        },
        onSubmitInfo: function (event) {
            var _this = this;
            event.stopPropagation();
            console.log('onSubmitInfo');
            $(".cropper-container").hide();
            var playerDoc = {};
            this.setProp(this, playerDoc);
            if (this.isEdit) {
                var postUpdate = function () {
                    _this.post('/admin/player/update', { playerDoc: playerDoc }, function (res) {
                        console.log(res);
                        _this.isEdit = false;
                        if (res) {
                            window.location.reload();
                        }
                    });
                };
                playerDoc.id = this.editPlayerId;
                if (this.isChangeAvatar) {
                    playerDoc.avatar = this.cropper.getCroppedCanvas().toDataURL();
                    postUpdate();
                    console.log('isChangeAvatar');
                }
                else {
                    postUpdate();
                }
            }
            else {
                if (this.isChangeAvatar) {
                    playerDoc.avatar = this.cropper.getCroppedCanvas().toDataURL();
                }
                this.$http.post('/admin/player/add', { playerData: playerDoc }, function (res) {
                    console.log(res);
                    if (res) {
                        window.location.reload();
                    }
                });
            }
        },
        showFile: function (e) {
            var _this = this;
            var fr = new FileReader();
            var image = document.getElementById('image');
            console.log("showFile", e.target.files[0]);
            fr.readAsDataURL(e.target.files[0]);
            fr.onload = function (e) {
                _this.isChangeAvatar = true;
                _this.imagePath = e.target.result;
                image.src = _this.imagePath;
                _this.stage = _this.initCanvas(_this.imagePath, 1);
                _this.cropper = new Cropper(image, {
                    aspectRatio: 180 / 76,
                    crop: function (e) {
                        _this.onUpdateCropPreview(e.detail);
                    }
                });
            };
        },
        onUpdateCropPreview: function (cropData) {
            var scale = cropData.width / 180;
            if (this.bluePlayerCard && this.bluePlayerCard.avatarBmp) {
                this.bluePlayerCard.avatarBmp.x = -cropData.x / scale;
                this.bluePlayerCard.avatarBmp.y = -cropData.y / scale;
                this.bluePlayerCard.avatarBmp.scaleX = this.bluePlayerCard.avatarBmp.scaleY = 1 / scale;
                this.redPlayerCard.avatarBmp.x = -cropData.x / scale;
                this.redPlayerCard.avatarBmp.y = -cropData.y / scale;
                this.redPlayerCard.avatarBmp.scaleX = this.redPlayerCard.avatarBmp.scaleY = 1 / scale;
            }
        },
        initCanvas: function (imagePath, scale) {
        },
        onFtSelected: function (v) {
            console.log('onFtSelected', v, this.ftId);
        }
    },
    ready: function () {
        var _this = this;
        _this_ = this;
        this.isEdit = false;
        this.isChangeAvatar = false;
        this.$on(const_1.ViewEvent.PLAYER_ADD, function (param) {
            _this.ftOptionArr = param.ftOptionArr.concat({ text: "退团保平安", value: 0 });
        });
        this.$on(const_1.ViewEvent.PLAYER_EDIT, function (param) {
            var playerId = param.playerId;
            _this.ftOptionArr = param.ftOptionArr.concat({ text: "退团保平安", value: 0 });
            _this.isEdit = true;
            _this.isChangeAvatar = false;
            _this.post("/db/player/" + playerId, function (data) {
                console.log('res: ', data);
                var playerDoc = data.playerDoc;
                _this.editPlayerId = playerDoc.id;
                _this.stage = _this.initCanvas(playerDoc.avatar, 1);
                _this.setProp(playerDoc, _this);
                _this.avatar = playerDoc.avatar;
            });
            console.log(const_1.ViewEvent.PLAYER_EDIT, playerId);
        });
    }
};



