"use strict";
var profile_1 = require("./profile");
var Command_1 = require("../../Command");
var const_1 = require("../../const");
var $ = require('jquery');
exports.PlayerView = {
    template: require('./player.html'),
    components: { Profile: profile_1.Profile },
    props: [
        'total',
        'ftId',
        'playerArr',
        'playerMap',
        'ftOptionArr',
    ],
    mounted: function () {
        var _this = this;
        console.log('player Ready!!');
        this.$http.post('/db/player', { all: true }).then(function (res) {
            console.log(res);
            _this.playerArr = res.body.PlayerArr;
        });
    },
    methods: {
        onClearActPlayerGameRec: function () {
            this.post("/panel/stage1v1/" + Command_1.CommandId.cs_clearActPlayerGameRec);
        },
        onSaveToTotalScore: function () {
            this.post("/panel/stage1v1/" + Command_1.CommandId.cs_saveToTotalScore);
        },
        onPickPlayer: function (playerId) {
            this.pickPlayerIdArr.push(playerId);
            if (this.pickPlayerIdArr.length == 4) {
                console.log('pick team');
                this.pickPlayerIdArrArr.push(this.pickPlayerIdArr);
                this.pickPlayerIdArr = [];
            }
            this.total = this.pickPlayerIdArrArr.length * 4 + this.pickPlayerIdArr.length;
        },
        onAddPlayer: function () {
            $('#modal-player').openModal();
            this.message = "添加球员";
            this.isOpen = true;
            this.$broadcast(const_1.ViewEvent.PLAYER_ADD, { ftOptionArr: this.ftOptionArr.concat() });
        },
        onAddPlayerList: function () {
            var a = [];
            for (var i = 0; i < this.pickPlayerIdArrArr.length; i++) {
                var playerIdArr = this.pickPlayerIdArrArr[i];
                a = a.concat(playerIdArr);
            }
            a = a.concat(this.pickPlayerIdArr);
            console.log('playerList', a);
            this.post("/panel/stage1v1/" + Command_1.CommandId.cs_setActPlayer, { playerIdArr: a });
        },
        onEdit: function (playerId, event) {
            event.stopPropagation();
            console.log("onEdit", playerId);
            $('#modal-player').openModal();
            this.message = "编辑球员";
            this.$broadcast(const_1.ViewEvent.PLAYER_EDIT, { playerId: playerId, ftOptionArr: this.ftOptionArr.concat() });
        },
        onFtSelected: function () {
            this.playerArr = [];
            for (var playerId in this.playerMap) {
                var playerDoc = this.playerMap[playerId];
                if (playerDoc.ftId == this.ftId)
                    this.playerArr.push(playerDoc);
            }
        },
    }
};



